.close-btn {
	width: 40px;
	height: 40px;
	background: var(--general-1);
	border: none;
}

.dialog-header {
	height: 60px;
	border-bottom: 1px solid var(--neutral-2);
}

.dialog-actions {
	height: 70px;
	border-top: 1px solid var(--neutral-2);
}

.mt-22px {
	margin-top: 22px;
}

.icon-container {
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: rgba(255, 92, 184, 0.1);
}

.icon-style {
	width: 20px;
	height: 20px;
	font-size: 20px;
}

.btn-text {
	vertical-align: super;
}
